.main_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    position: sticky;
    bottom: 0;
    width: 100%;
    background-color: #EEEEEE;
}

.one_icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.text {
    font-weight: 400;
    font-size: 12px;
}
